import React,{DSTable,DSComponent,Fragment} from 'comp/index';
import './index.less'

import { Form,Input, Button,Row,Col,Breadcrumb,Space} from 'antd';
import {PlusOutlined } from '@ant-design/icons';
class UserView extends DSComponent{   
    constructor(props){
        super(props);
        this.tableRef = React.createRef();
        this.searchFormRef = React.createRef();
        this.state = {searchCondition:props.searchCondition};
    }
    static defaultProps = {
        searchCondition:{}
    }
    onSearch=async(e)=>{
        this.setState(state=>{
            state.searchCondition = e;
            return state;
        },()=>{
            this.tableRef.current.reload();//
        });
    }
    onReset=()=>{
        const {searchCondition} = this.props;
        this.setState(state=>{
            state.searchCondition = searchCondition;
            return state;
        },()=>{
            this.searchFormRef.current.resetFields();//
            this.tableRef.current.reload();//
        });
    }
    onReload=()=>{
        this.tableRef.current.reload();
    }
    render(){
        const {searchCondition} = this.state;
        const columns=[
        {title: '',dataIndex: 'aliasName'},
        {title: '',dataIndex: 'account'},
        {title: '',dataIndex: 'email'},
        {title: '',dataIndex: 'phone'},
        {title: '',dataIndex: 'status'},
        {title: '',dataIndex: 'lastLoginIp'},
        {title: '',width:160,render:(value,item,index)=>{
            return (
            <Space>
                <Button type="link" ></Button>
                <Button type="link" ></Button>
                <Button type="link" ></Button>
                <Button type="link" ></Button>
            </Space>
            );
        }}];
        
        return (
        <Fragment>
            <div className='ds-back-layout'>
                <Breadcrumb className='ds-crumb'>
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item></Breadcrumb.Item>
                </Breadcrumb>
                <div className='ds-search'>
                    <div className='ds-search-title'>
                        <Row wrap={false}>
                            <Col flex="auto"></Col>
                            {/* <Col flex="100px" style={{textAlign:'right'}}></Col> */}
                        </Row>
                    </div>
                    <div className='ds-search-wrap'>
                        <Form layout="inline" ref={this.searchFormRef} initialValues={searchCondition} onFinish={this.onSearch}>
                            <Form.Item name="name" label="">
                                <Input placeholder="" autoComplete="off"/>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="submit"></Button>
                            </Form.Item>
                            <Form.Item>
                                <Button onClick = {this.onReset}></Button>
                            </Form.Item>
                        </Form>
                    </div>
                </div>

                <div className='ds-table'>
                    <div className='ds-table-title'>
                    <Row wrap={false}>
                        <Col flex="auto"></Col>
                        <Col flex="100px" style={{textAlign:'right'}}>
                            <Button type="primary" icon={<PlusOutlined/>}></Button>
                        </Col>
                    </Row>
                    </div>
                    <div className='ds-table-wrap'>
                        <DSTable columns={columns} searchCondition={searchCondition} path={'/api/user/find'} ref={this.tableRef}></DSTable>
                    </div>
                </div>
            </div>

        </Fragment>
        );
    }
}
export default UserView;